import Ajv from 'ajv';
const ajv = new Ajv();
const schema = {
    type: "object",
    definitions: {
        links: {
            type: "array",
            items: {
                type: "object",
                properties: {
                    url: {
                        type: "string",
                    },
                    statusCode: {
                        type: "number"
                    },
                    method: {
                        type: "string"
                    },
                    body: {
                        type: "string"
                    }
                },
                additionalProperties: false,
                required: ["url", "statusCode", "method"]
            },
        }
    },
    properties: {
        links: { $ref: "#/definitions/links" }
    },
    additionalProperties: false,
    required: ["links"]
};
const validateSchema = ajv.compile(schema);
export const isInputFileValid = (data) => {
    return !!validateSchema(data);
};
